import { uniPopup } from "@dcloudio/uni-ui";
export default {
  name: "keyboader",
  data() {
    return {
      visible_dt: null,
      province_0: ["京", "津", "沪", "渝", "冀", "豫", "云", "辽", "黑", "湘"],
      province_1: ["皖", "鲁", "新", "苏", "浙", "赣", "鄂", "桂", "甘"],
      province_2: ["晋", "蒙", "陕", "吉", "闽", "贵", "粤"],
      province_3: ["青", "藏", "川", "宁", "琼"],
      letter_0: ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"],
      letter_1: ["A", "S", "D", "F", "G", "H", "J", "K", "L"],
      letter_2: ["Z", "X", "C", "V", "B", "N", "M"],
      num: ["1", "2", "3", "4", "5", "6", "7", "8", "9", "0"],
      tep_act: 0,
      value: ""
    };
  },
  methods: {
    clickPro(item,e) {
      let a = this.tep_act + 1;
      let b = this.value + item;
      this.tep_act = a
      this.value = b
        console.log(this.value)
      this.$emit("changeValue", this.value);
    },
    closeKeyboard(){
      this.$emit("close");
    },
    clickLet(item,e) {
      var that = this;
      if (this.tep_act <= 8) {
        let a = this.tep_act + 1;
        let b = this.value +item;
        this.tep_act = a
        this.value = b
        console.log(this.value)
        this.$emit("changeValue", this.value);
      }
    },
    clickNum(item, e) {
      var that = this;
      if (this.tep_act <= 8) {
        let a = this.tep_act + 1;
        let b = this.value + item;
        this.tep_act = a
        this.value = b
        console.log(this.value)
        this.$emit("changeValue", this.value);
      }
    },
    change(e){
      if(e.show){
      }else{
      this.$emit("close");
      }
    },
    clickdele() {
      var that = this;
      if (this.tep_act != 0) {
        let a = this.tep_act - 1;
        let c = this.value;
        let b = c.substr(0, c.length - 1);
        this.tep_act = a
        this.value = b
        console.log(this.value)
        this.$emit("changeValue", this.value);
      } else {
        this.$emit("changeValue", "");
      }
    }
  },
  props: {
    visible: {
      type: Boolean
    }
  },
  components: { uniPopup },
  watch: {
    visible(val) {
      console.log(val,"ss")
      this.visible_dt = val;
    },
    visible_dt(val) {
      console.log(val)
      if (val) {
        this.$refs.popup.open();
      } else {
        this.$refs.popup.close();
      }
    }
  },
  mounted() {
    this.visible_dt = this.visible_dt;
  }
};
